import React from 'react';
import { withRouter } from 'react-router-dom';
import './paw.styles.scss';

const Paw = ({title, imageUrl, size, history, linkUrl, match}) => (
  <div className={`${size} paw`} onClick={() => history.push(`${match.url}${linkUrl}`)}>
    <div 
      className="background-image"  
      style={{
        backgroundImage: `url(${imageUrl})`
      }} />
    <div className="content">
      <h1 className="title">{title.toUpperCase()}</h1>
      <span className="subtitle">PET NOW</span>
    </div>
  </div>
)

export default withRouter(Paw);